/**
 * GeChiUI dependencies
 */
import { TextControl } from '@gechiui/components';
import { useEntityProp } from '@gechiui/core-data';
import { __ } from '@gechiui/i18n';

export default function NavigationMenuNameControl() {
	const [ title, updateTitle ] = useEntityProp(
		'postType',
		'gc_navigation',
		'title'
	);

	return (
		<TextControl
			label={ __( 'Menu name' ) }
			value={ title }
			onChange={ updateTitle }
		/>
	);
}
