/**
 * GeChiUI dependencies
 */
import { __, _x } from '@gechiui/i18n';
import deprecated from '@gechiui/deprecated';
import { store as coreStore } from '@gechiui/core-data';
import {
	MenuGroup,
	MenuItemsChoice,
	PanelBody,
	SelectControl,
	ToolbarDropdownMenu,
	ToolbarGroup,
} from '@gechiui/components';
import { useMemo } from '@gechiui/element';
import { BlockControls, InspectorControls } from '@gechiui/block-editor';
import { useSelect } from '@gechiui/data';

/**
 * Internal dependencies
 */
import InnerBlocks from './inner-blocks';
import PlaceholderPreview from '../navigation/edit/placeholder/placeholder-preview';

function NavigationAreaBlock( { attributes, setAttributes } ) {
	const { area } = attributes;

	const { navigationAreas, hasResolvedNavigationAreas } = useSelect(
		( select ) => {
			const { getEntityRecords, hasFinishedResolution } = select(
				coreStore
			);
			return {
				navigationAreas: getEntityRecords( 'root', 'navigationArea' ),
				hasResolvedNavigationAreas: hasFinishedResolution(
					'getEntityRecords',
					[ 'root', 'navigationArea' ]
				),
			};
		}
	);
	const navigationMenuId = navigationAreas?.length
		? navigationAreas[ area ]
		: undefined;

	const choices = useMemo(
		() =>
			navigationAreas?.map( ( { name, description } ) => ( {
				label: description,
				value: name,
			} ) ),
		[ navigationAreas ]
	);

	deprecated( 'gc.blockLibrary.NavigationArea', {
		since: '5.9',
	} );

	return (
		<>
			<BlockControls>
				<ToolbarGroup>
					<ToolbarDropdownMenu
						label={ __( 'Select Area' ) }
						text={ __( 'Select Area' ) }
						icon={ null }
					>
						{ ( { onClose } ) => (
							<MenuGroup>
								<MenuItemsChoice
									value={ area }
									onSelect={ ( selectedArea ) => {
										setAttributes( { area: selectedArea } );
										onClose();
									} }
									choices={ choices }
								/>
							</MenuGroup>
						) }
					</ToolbarDropdownMenu>
				</ToolbarGroup>
			</BlockControls>
			<InspectorControls>
				<PanelBody title={ __( 'Options' ) }>
					<SelectControl
						label={ _x( 'Area' ) }
						value={ area }
						// `undefined` is required for the preload attribute to be unset.
						onChange={ ( value ) =>
							setAttributes( {
								area: value,
							} )
						}
						options={ choices }
					/>
				</PanelBody>
			</InspectorControls>
			{ ! hasResolvedNavigationAreas && <PlaceholderPreview isLoading /> }
			{
				// Render inner blocks only when navigationMenuId is known so
				// that inner blocks template is correct from the start.
				hasResolvedNavigationAreas && (
					<InnerBlocks navigationMenuId={ navigationMenuId } />
				)
			}
		</>
	);
}

export default NavigationAreaBlock;
