/**
 * GeChiUI dependencies
 */
import { createBlock } from '@gechiui/blocks';

const transforms = {
	from: [
		{
			type: 'raw',
			schema: {
				'gc-block': { attributes: [ 'data-block' ] },
			},
			isMatch: ( node ) =>
				node.dataset && node.dataset.block === 'core/nextpage',
			transform() {
				return createBlock( 'core/nextpage', {} );
			},
		},
	],
};

export default transforms;
