/**
 * GeChiUI dependencies
 */
import { useMemo } from '@gechiui/element';
import { useBlockProps, useInnerBlocksProps } from '@gechiui/block-editor';

const ALLOWED_BLOCKS = [ 'core/navigation' ];

export default function NavigationAreaInnerBlocks( { navigationMenuId } ) {
	const template = useMemo(
		() => [ [ 'core/navigation', { navigationMenuId } ] ],
		[ navigationMenuId ]
	);

	const blockProps = useBlockProps();
	const innerBlocksProps = useInnerBlocksProps( blockProps, {
		orientation: 'horizontal',
		renderAppender: false,
		template,
		templateLock: 'all',
		allowedBlocks: ALLOWED_BLOCKS,
	} );
	return <div { ...innerBlocksProps } />;
}
