/**
 * GeChiUI dependencies
 */
import { SearchControl } from '@gechiui/components';
import { useState } from '@gechiui/element';

/**
 * Internal dependencies
 */
import TemplatePartPreviews from './template-part-previews';

export default function TemplatePartSelection( {
	setAttributes,
	onClose,
	area,
	templatePartId = null,
} ) {
	const [ filterValue, setFilterValue ] = useState( '' );
	return (
		<div>
			<SearchControl
				value={ filterValue }
				onChange={ setFilterValue }
				className="gc-block-template-part__selection-preview-search-form"
			/>
			<div className="gc-block-template-part__selection-preview-container">
				<TemplatePartPreviews
					setAttributes={ setAttributes }
					filterValue={ filterValue }
					onClose={ onClose }
					area={ area }
					templatePartId={ templatePartId }
				/>
			</div>
		</div>
	);
}
